import {ACTION_TYPES} from "@meddbriefer/feedback-data/constants"

export const formatAsMinSecs = (seconds) => {
    return new Date(seconds * 1000).toISOString().substr(14, 5)
}

export function capitalizeFirstLetter(str) {
    return `${str[0].toUpperCase()}${str.slice(1)}`
}
export function lowerCaseFirstLetter(str) {
  return `${str[0].toLowerCase()}${str.slice(1)}`;
}


export const getStatus = (ieSummary, id) => {
    const sts = ieSummary[id].status;
    // console.log(`status for ${id}: ${sts}`)
    return sts;
  };

const ERROR_TYPES = ["error", "errors", "misorderedError"]
const OUT_OF_ORDER_TYPES = ["misordered", "misorderedError"]

export const isGood = (ieSummary, id) => "good" === getStatus(ieSummary, id);
export const isAbsent = (ieSummary, id) => "absent" === getStatus(ieSummary, id);
export const hasError = (ieSummary, id) => ERROR_TYPES.includes(getStatus(ieSummary, id));
export const isOutOfOrder = (ieSummary, id) => OUT_OF_ORDER_TYPES.includes(getStatus(ieSummary, id));


export const getRequiredSubActions = (slide) =>
    slide.subActions.filter((a) =>
      ["header", "required"].includes(a.type.toLowerCase())
    );

export const getNotIndicatedSubActions = (slide) => {
  return slide.subActions.filter((e) => e.type === ACTION_TYPES.NOTIND);
}


export const computeReviewTitle = (obsLog, scenario) => {
  return `${scenario.name} / ${obsLog.userName} / ${obsLog.label}`
}

export const computeDebriefingTitle = (obsLog, scenario) => {
  return `${scenario.name}`;
};

export const groupByKey = (list, key) =>
  list.reduce(
    (hash, obj) => ({
      ...hash,
      [obj[key]]: (hash[obj[key]] || []).concat(obj),
    }),
    {}
  );
