import React, {useContext} from "react"
import { Button } from "reactstrap";
import { useHistory } from "react-router-dom";

import { AuthContext } from "@meddbriefer/mdb-auth";
import { MDBModal } from "@meddbriefer/mdb-ui";

import { useDebriefing } from "../DebriefingContext";

export default function SaveDebriefingModal({ show, dismiss }) {
  const {saveUserData, getUserData} = useContext(AuthContext)
  let history = useHistory()
  const {nextURL, saveDebriefing, scenario, startTime} = useDebriefing()
  
  const submitHandler = async (event) => {
    event.preventDefault();
    await saveDebriefing()
    const userData = await getUserData()
    const timeOnTask = userData.timeOnTask
      ? [...userData.timeOnTask]
      : [];
    timeOnTask.push({
      scenario: scenario.name,
      task: "debriefing",
      startTime: startTime,
      endTime: Date.now()
    })
    console.table("timeOnTask", timeOnTask);
    await saveUserData("timeOnTask", timeOnTask)
    history.push(nextURL)
  };

  return (
    <MDBModal
      title="Are you finished with this debriefing?"
      show={show}
      dismiss={dismiss}
    >
      <div className="container">
        <div className="row">
          <div className="col-12">
            <p>
              <span className="text-danger">WARNING: </span>
              <span>
                Clicking "I'm Done" will not allow you to view the debriefing
                any longer. Do not click on it unless you are truly done with
                this debriefing.
              </span>
            </p>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <Button color="danger" size="lg" onClick={submitHandler}>
              I'm Done
            </Button>
            <Button color="primary" size="lg" onClick={dismiss}>
              Cancel
            </Button>
          </div>
        </div>
      </div>
    </MDBModal>
  );
}
