//main function for constraint checking
export const checkAgainstKB = (phases, phaseNames,requiredPhaseNames,requiredSubphases,checklistHierarchy,itemByID,itemByLabel,constraintsViolated,phaseFB) => {

    let result = collectEntriesToCheckConstraints(phases, phaseNames, requiredPhaseNames, requiredSubphases, checklistHierarchy)
    let entryNames = result[0]
    let uniqueEntryNames = result[1]
    let entries = result[2]
    uniqueEntryNames.forEach(firstArg => {
        let firstArgItem = getItem(firstArg,itemByID,itemByLabel)
        if (!!firstArgItem) {
            processFirstArg(firstArgItem,entryNames,entries,itemByID,itemByLabel,constraintsViolated)}})

    let aggregatedConstraints = aggregateArguments(constraintsViolated)
    generateConstraintFB(aggregatedConstraints, itemByID, itemByLabel, phases, phaseNames, checklistHierarchy, phaseFB)
    //only expect to change objects in phases
    return [phases, phaseNames]
}

const processFirstArg = (firstArgItem, entryNames, entries,itemByID,itemByLabel,constraintsViolated) => {
    let constraint = getConstraint(firstArgItem)  //will find only one constraint per 1st arg
    if (!!constraint) {
        checkConstraintsForArg(firstArgItem, constraint, entryNames, entries,itemByID,itemByLabel,constraintsViolated)}
}
